import React from 'react';
import axios from 'axios';

import MainFrame from '../components/MainFrame';
import Item from '../components/Item';

class Level6 extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isLoading : true,
        };
    }
    componentDidMount(){
        const key = "AKfycbzFr6n2s4btj6liWhCL2IBFGEGPqwHg7Y1YlNQx_325MVP4cZDW";
        const url = `https://script.google.com/macros/s/${key}/exec`;
        axios.get(url)
        .then( (res) => {
            this.setState({
                isLoading : false,
                docsData : res.data.result,
            });
        })
    }

    render(){
        let title="";
        let desc="";
        return(
            <MainFrame>
                {/* <Item head
                    title = "6층"
                    desc = "asdf"
                /> */}
                {this.state.isLoading == true ?
                    <div>Loading... </div> :
                    this.state.docsData.map((x) => (<div>{`${x}`}</div>) )
                }   
            </MainFrame>
        );
    }
}

export default Level6;